.slider {
  width: 100%;
  //height: 750px;
  height: 100%;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;

  @include tablets {
    flex-wrap: nowrap;
  }

  &__description {
    height: 100%;
    width: 50%;
    background-color: #f0efe9;
    display: flex;
    flex-direction: column;
    justify-content: center;

    @include tablets {
      width: 100%;
      height: rem(270px);
      order: 5;
    }
  }

  &__description--info {
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
  }

  &__title {
    margin-bottom: rem(39px);
    @include phones {
      text-align: center;
    }
    @include tablets {
      text-align: center;
    }
  }

  &__text {
    font-family: robotomedium;
    font-size: rem(16px);
    font-weight: 500;
    color: $light;
    text-transform: uppercase;
    margin-bottom: rem(39px);
    position: relative;

    @include phones {
      font-size: rem(14px);
    }
  }

  &__text:before {
    content: '';
    width: 70%;
    height: rem(1px);
    background-color: $light;
    position: absolute;
    top: rem(-16px);
    left: 50%;
    transform: translateX(-50%);
    opacity: .4;
  }

  &__text:after {
    content: '';
    width: 70%;
    height: rem(1px);
    background-color: $light;
    position: absolute;
    left: 50%;
    bottom: rem(-16px);
    transform: translateX(-50%);
    opacity: .4;
  }

  &__link-block {
    display: flex;
    flex-direction: row-reverse;
    justify-content: center;
  }

  &__link {
    padding: rem(17px) rem(21px) rem(17px) rem(41px);
    position: relative;
    background-color: $light;
    color: #fff;
    font-family: robotomedium;
    font-weight: 500;
    font-size: rem(16px);
    border-radius: rem(5px);
    transition: .3s;

    @include phones {
      font-size: rem(14px);
      padding: rem(12px) rem(20px) rem(12px) rem(52px);
    }
  }

  &__link:hover {
    background-color: $green;
  }

  &__link--pic {
    position: absolute;
    left: rem(15px);
    bottom: rem(14px);

    @include phones {
      left: rem(20px);
      bottom: rem(7px);
    }
  }

  &__main-pic {
    width: 50%;
    height: 65%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    background-color: #efebe0;

    @include tablets {
      width: 100%;
      height: auto;
      padding-bottom: 45px;
    }
  }

  &__main-pic--block {
    width: 100%;
  }

  &__main-pic--img {
    width: 100%;
    height: 100%;
  }

  &__menu {
    width: 50%;
    height: 35%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    background-color: #efebe0;

    @include tablets {
      width: 100%;
      height: auto;
      order: 10;
    }
  }

  &__btn-block {
    width: 100%;
    max-height: rem(225px);
    display: flex;
    flex-direction: row;
    cursor: pointer;
  }

  &__menu--prev-img, .slider__menu--next-img {
    width: 50%;
    //height: 100%;
    position: relative;
  }

  &__prev-pic--img, .slider__next-pic--img {
    width: 100%;
    height: 100%;
  }

  &__menu--prev, .slider__menu--next {
    width: rem(43px);
    height: rem(27px);
    position: absolute;
    top: 50%;
    left: 43%;
    z-index: 5;
    transition: .4s;
  }
}

.slider__menu--prev:hover, .slider__menu--next:hover {
  transform: scale(1.3);
}

.slider__menu--prev-img:after, .slider__menu--next-img:after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color:rgba(182, 180, 174, 0.7);
  transition: .3s;
}

.link-svg {
  width: rem(20px);
  height: rem(20px);
  fill: white;
}

.arrow-down, .arrow-up {
  height: 100%;
  width: 100%;
