.carousel-main{
  position: relative;
}

.carousel-main__slide{
  position: absolute;
  z-index: 1;
  opacity: 0;
  transition: all 1s;
  &.active{
    z-index: 10;
    opacity: 1;
  }
}

.slider-screen__img{
  max-width: 100%;
  max-height:100%;

}

.slider__btns{
  height: 30%;
  width: 50%;
  display: flex;
  background-color: #efebe0;

  @include netbook{
    height: rem(200px);
    width: 100%;
  }
}

.carousel-slides__slide{
  position: absolute;
  left: 0;
  height: 100%;
  width: 100%;
  z-index: 30;
  display: flex;
  justify-content: center;
  align-items: center;
}

.carousel-slides__slide-previous{
  bottom: 100%;
  opacity:0;
  z-index: -10;
  transition: top .75s, bottom .75s, z-index .75s;
  &.active{
    bottom: 0;
    z-index: 10;
    opacity: 1;
  }
  &.next-active{
    bottom:-100%;
    z-index: 5;
    opacity: 1;
  }
  &.old-active{
    bottom:100%;
    z-index: 10;
    opacity: 1;
  }
}

.carousel-slides__slide-next{
  bottom: 100%;
  opacity:0;
  z-index: -10;
  transition: top .75s, bottom .75s, z-index .75s;
  &.active{
    bottom: 0;
    z-index: 10;
    opacity: 1;
  }
  &.next-active{
    bottom:-100%;
    z-index: 5;
    opacity: 1;
  }
  &.old-active{
    bottom:100%;
    z-index: 10;
    opacity: 1;
  }
}

.slider__link{
  width: 50%;
  cursor: pointer;
  display: flex;
  border:none;
  background-color: transparent;
  padding: 0;
  position: relative;
  overflow: hidden;

  &:before{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(#b6b4ae,.7);
    z-index: 40;
  }
}

.previous-slide{
  outline:none;
  &:after{
    content: '';
    position: absolute;
    top: 50%;
    display: block;
    width: 1.99rem;
    height: rem(20px);
    background:url("../images/img/arrow-up.png") no-repeat;
    background-size: cover;
    left: 50%;
    transform:translate(-50%,-50%);
    z-index: 50;
  }
  &:hover{
    &:after{
      top: 60%;
      transition: top .3s;
    }
  }
}

.next-slide{
  outline:none;
  &:after{
    content: '';
    position: absolute;
    display: block;
    width: 1.99rem;;
    height: rem(20px);
    background:url("../images/img/arrow-down.png") no-repeat;
    background-size: cover;
    left: 50%;
    transform:translate(-50%,-50%);
    top: 50%;
    z-index: 50;
  }
  &:hover{
    &:after{
      top: 40%;
      transition: top .3s;
    }
  }
}

.slider__img{
  max-width: 100%;
  max-height: 100%;
  flex: auto;
}
